import React, { useState, useContext } from 'react';
import { useNavigate } from 'react-router-dom';
import { TokenContext } from '../../tokenContext';
import 'bootstrap/dist/css/bootstrap.min.css';

const Login = () => {
  const [loginId, setLoginId] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState(null);
  const navigate = useNavigate();
  const { login } = useContext(TokenContext); //     

  const handleLogin = async (e) => {
    e.preventDefault();
    setError(null);

    try {
      const response = await fetch('http://localhost:8080/auth/login', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ loginId, password }),
      });

      if (!response.ok) {
        throw new Error(' ');
      }

      const data = await response.json();
      login(data.accessToken); //     
      navigate('/'); //      
    } catch (error) {
      console.error(' :', error);
      setError(' .  .');
    }
  };

  const buttonStyle = {
    backgroundColor: '#f0f0f0', //     
    color: '#000',
    border: '1px solid #ccc', //    
    transition: 'background-color 0.3s, color 0.3s',
    width: '48%' //   
  };

  const handleMouseEnter = (e) => {
    e.target.style.backgroundColor = '#343a40';
    e.target.style.color = '#fff';
  };

  const handleMouseLeave = (e) => {
    e.target.style.backgroundColor = '#f0f0f0';
    e.target.style.color = '#000';
  };

  return (
    <div className="d-flex flex-column align-items-center justify-content-center vh-100" style={{ backgroundColor: '#f8f9fa' }}>
      <h1 className="mb-5"></h1>
      <div className="card p-4" style={{
        width: '400px',
        boxShadow: '0 4px 8px rgba(0, 0, 0, 0.1)'
      }}>
        <h5 className="text-center mb-4" style={{ fontWeight: 'bold' }}>   </h5>
        {error && <div className="alert alert-danger">{error}</div>} {/*    */}
        <form onSubmit={handleLogin}> {/*     */}
          <div className="form-group mb-3">
            <label htmlFor="loginId"></label>
            <input type="text" className="form-control" id="loginId" placeholder="" value={loginId} onChange={(e) => setLoginId(e.target.value)} />
          </div>
          <div className="form-group mb-4">
            <label htmlFor="password"></label>
            <input type="password" className="form-control" id="password" placeholder="" value={password} onChange={(e) => setPassword(e.target.value)} />
          </div>
          <div className="d-flex justify-content-between">
            <button
              type="submit"
              className="btn"
              style={buttonStyle}
              onMouseEnter={handleMouseEnter}
              onMouseLeave={handleMouseLeave}
            >
              
            </button>
            <button
              type="button"
              className="btn"
              style={buttonStyle}
              onMouseEnter={handleMouseEnter}
              onMouseLeave={handleMouseLeave}
              onClick={() => navigate('/auth/select')}
            >
              
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default Login;
