import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import axios from 'axios';
import StarRating from '../../components/StarRating';
import ListPagination from '../../components/ListPagination';

const ItemDetail = () => {
    const { itemId } = useParams(); // URL itemId  
    const [pageNumber, setPageNumber] = useState(0);
    const [totalPages, setTotalPages] = useState(0);
    const [itemDetail, setItemDetail] = useState(null);
    const [reviews, setReviews] = useState([]);
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(null);

    //        
    const fetchItemDetail = async (page) => {
        setLoading(true);
        setError(null);

        try {
            const response = await axios.get(`http://localhost:8080/item/detail/${itemId}`);
            setItemDetail(response.data); //       
            setReviews(response.data.reviews.content); // Page     
            setTotalPages(response.data.reviews.totalPages); //    
        } catch (error) {
            console.error('     .:', error);
            setError('     .');
        } finally {
            setLoading(false);
        }
    };

    useEffect(() => {
        if (itemId) {
            fetchItemDetail(pageNumber); //       
        }
    }, [itemId, pageNumber]);

    const handlePageChange = (newPage) => {
        setPageNumber(newPage);
    };

    const formatCurrency = (amount) => {
        return Math.floor(amount).toLocaleString();
    };

    return (
        <>
            {loading && <p>Loading...</p>}
            {error && <p>{error}</p>}
            {itemDetail && (
                <div className="container product-detail-container">
                    <div className="breadcrumb"></div>
                    <div className="row">
                        <div className="col-md-6">
                            <img src={itemDetail.titleImageUrl} alt={itemDetail.name} className="img-fluid" />
                        </div>
                        <div className="col-md-6">
                            <h1>{itemDetail.itemTitle}</h1>
                            <StarRating rating={itemDetail.averageRating} /><span>{itemDetail.averageRating} ({itemDetail.reviewCount} )</span>
                            <p><strong>{itemDetail.discount}% <del>{formatCurrency(itemDetail.price)}</del></strong></p>
                            <p><strong className="price">{formatCurrency(itemDetail.discountPrice)}</strong></p>
                            <p>: {itemDetail.memberName}</p>
                            <p>  : {itemDetail.stockQuantity}</p>
                            <div className="d-flex align-items-center mb-3">
                                <input type="number" className="form-control w-25 mr-2" defaultValue="1" />
                                <button className="btn btn-outline-secondary mr-2"> </button>
                                <button className="btn btn-primary"></button>
                            </div>
                        </div>
                    </div>
                    <div className="product-description my-5">
                        <h2> </h2>
                        <img src={itemDetail.contentImageUrl || '/default-image.jpg'} alt="" />
                        <p>{itemDetail.description}</p>
                    </div>
                    <div className="product-reviews my-5">
                        <h2> </h2>
                        <StarRating rating={itemDetail.averageRating} /> <span>{itemDetail.averageRating} ({itemDetail.reviewCount} )</span>
                        <div className="reviews-list my-3">
                            {reviews.map((review, index) => (
                                <div key={index} className="review-item">
                                    <div>{review.memberName}</div>
                                    <div className="review-header d-flex align-items-center justify-content-between">
                                        <div>
                                            <StarRating rating={review.ratingValue} /><span>{review.date}</span>
                                        </div>
                                    </div>
                                    <div><strong>{review.summary}</strong></div>
                                    <div>
                                        <p>{review.description}</p>
                                    </div>
                                </div>
                            ))}
                        </div>
                        <ListPagination
                            currentPage={pageNumber}
                            totalPages={totalPages}
                            onPageChange={handlePageChange}
                        />
                    </div>
                </div>
            )}
        </>
    );
};

export default ItemDetail;
