import React, {useState, useEffect} from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.bundle.min';
import Header from '../../components/Header';
import Footer from "../../components/Footer";
import '../../styles/SaleHistory.css'; //  CSS  .

const SaleHistory = () => {
  const [sales, setSales] = useState([]);

  useEffect(() => {
    //  API        .
    //    .
    const fetchSales = async () => {
      const exampleSales = [
        {
          category: '/',
          productName: ' ',
          productPrice: '238000',
          quantity: 20,
          totalAmount: '5660000',
          buyerId: 'qwerasdf1234',
          buyerName: '',
          buyerPhone: '010-9999-8888',
          saleTime: '2024-05-25 17:56:32',
        },
        //      
      ];
      setSales(exampleSales);
    };

    fetchSales();
  }, []);

  return (

        <div className="body-background">
          <div className="container sale-history-container">
            <h3 className="fs-4" style={{fontWeight: 'bold'}}> </h3>
            <div className="table-responsive">
              <table className="table table-bordered">
                <thead className="thead-dark">
                <tr>
                  <th></th>
                  <th></th>
                  <th></th>
                  <th></th>
                  <th>  </th>
                  <th> ID</th>
                  <th> </th>
                  <th> </th>
                  <th> </th>
                </tr>
                </thead>
                <tbody>
                {sales.map((sale, index) => (
                    <tr key={index}>
                      <td>{sale.category}</td>
                      <td>{sale.productName}</td>
                      <td>{sale.productPrice}</td>
                      <td>{sale.quantity}</td>
                      <td>{sale.totalAmount}</td>
                      <td>{sale.buyerId}</td>
                      <td>{sale.buyerName}</td>
                      <td>{sale.buyerPhone}</td>
                      <td>{sale.saleTime}</td>
                    </tr>
                ))}
                </tbody>
              </table>
            </div>
            <nav aria-label="Page navigation example">
              <ul className="pagination justify-content-center">
                <li className="page-item disabled">
                  <a className="page-link" href="#" tabIndex="-1">Previous</a>
                </li>
                {[...Array(10).keys()].map(num => (
                    <li className="page-item" key={num}>
                      <a className="page-link" href="#">{num + 1}</a>
                    </li>
                ))}
                <li className="page-item">
                  <a className="page-link" href="#">Next</a>
                </li>
              </ul>
            </nav>
          </div>
        </div>

  );
};

export default SaleHistory;