import React, { useState, useEffect } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.min.css';

const MemberJoin = () => {
  const location = useLocation();
  const navigate = useNavigate();
  const [formData, setFormData] = useState({
    loginId: '',
    name: '',
    email: '',
    phone: '',
    address: '',
    password1: '',
    password2: '',
    role: ''
  });
  const [errors, setErrors] = useState({});
  const [generalError, setGeneralError] = useState(null);

  useEffect(() => {
    if (location.state && location.state.role) {
      setFormData((prevData) => ({ ...prevData, role: location.state.role }));
    }
  }, [location.state]);

  const handleChange = (e) => {
    setFormData({ ...formData, [e.target.id]: e.target.value });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setErrors({});
    setGeneralError(null);

    if (formData.password1 !== formData.password2) {
      setErrors({ password2: '  .' });
      return;
    }

    try {
      const response = await fetch('http://localhost:8080/auth/signup', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(formData)
      });

      if (!response.ok) {
        const errorData = await response.json();
        setErrors(errorData);
      } else {
        navigate('/auth/joincomplete', { state: { role: formData.role } });
      }
    } catch (error) {
      setGeneralError(' .  .');
    }
  };

  const buttonStyle = {
    backgroundColor: '#f0f0f0', //     
    color: '#000',
    border: '1px solid #ccc', //    
    transition: 'background-color 0.3s, color 0.3s',
    width: '50%' //   
  };

  const handleMouseEnter = (e) => {
    e.target.style.backgroundColor = '#343a40';
    e.target.style.color = '#fff';
  };

  const handleMouseLeave = (e) => {
    e.target.style.backgroundColor = '#f0f0f0';
    e.target.style.color = '#000';
  };

  return (
    <div className="d-flex flex-column align-items-center justify-content-center" style={{ minHeight: '100vh', backgroundColor: '#f8f9fa', padding: '20px 0' }}>
      <h1 className="mb-5"></h1>
      <div className="card p-4" style={{ width: '400px', boxShadow: '0 4px 8px rgba(0, 0, 0, 0.1)' }}>
        <h5 className="text-center mb-4">   </h5>
        {generalError && <div className="alert alert-danger">{generalError}</div>}
        <form onSubmit={handleSubmit}>
          <div className="form-group mb-3">
            <label htmlFor="loginId"></label>
            <input type="text" className="form-control" id="loginId" placeholder="" value={formData.loginId} onChange={handleChange} />
            {errors.loginId && <div className="text-danger">{errors.loginId}</div>}
          </div>
          <div className="form-group mb-3">
            <label htmlFor="name"></label>
            <input type="text" className="form-control" id="name" placeholder="" value={formData.name} onChange={handleChange} />
            {errors.name && <div className="text-danger">{errors.name}</div>}
          </div>
          <div className="form-group mb-3">
            <label htmlFor="email"></label>
            <input type="text" className="form-control" id="email" placeholder="" value={formData.email} onChange={handleChange} />
            {errors.email && <div className="text-danger">{errors.email}</div>}
          </div>
          <div className="form-group mb-3">
            <label htmlFor="phone"> </label>
            <input type="text" className="form-control" id="phone" placeholder=" " value={formData.phone} onChange={handleChange} />
            {errors.phone && <div className="text-danger">{errors.phone}</div>}
          </div>
          <div className="form-group mb-3">
            <label htmlFor="address"></label>
            <input type="text" className="form-control" id="address" placeholder="" value={formData.address} onChange={handleChange} />
            {errors.address && <div className="text-danger">{errors.address}</div>}
          </div>
          <div className="form-group mb-3">
            <label htmlFor="password1"></label>
            <input type="password" className="form-control" id="password1" placeholder="" value={formData.password1} onChange={handleChange} />
            {errors.password1 && <div className="text-danger">{errors.password1}</div>}
          </div>
          <div className="form-group mb-3">
            <label htmlFor="password2"> </label>
            <input type="password" className="form-control" id="password2" placeholder=" " value={formData.password2} onChange={handleChange} />
            {errors.password2 && <div className="text-danger">{errors.password2}</div>}
          </div>
          <div className="form-group mb-3">
            <label htmlFor="role"> </label>
            <input type="text" className="form-control" id="role" placeholder=" " value={formData.role} readOnly />
            {errors.role && <div className="text-danger">{errors.role}</div>}
          </div>
          <div className="d-flex justify-content-center">
            <button
              type="submit"
              className="btn"
              style={buttonStyle}
              onMouseEnter={handleMouseEnter}
              onMouseLeave={handleMouseLeave}
            >
               
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default MemberJoin;
